import React, { useEffect, useState } from 'react';
import styled from 'styled-components';

import axios from 'axios-instance';

import Workspace from "components/Workspace";
import Header from "components/Header";
import Spinner from 'components/Spinner';
import Label from 'components/Label';
import RecipeItem from "components/RecipeItem";
import Status from 'components/Status';

const Flex = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
`;
const RecipeWrapper = styled.div`
  display: grid;
  width: 300px;
  border: 1px solid transparent;
  border-radius: 5px;
  transition: all 0.2s ease-in-out;
  padding: 20px 12px;
  margin: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.25), 0 2px 2px rgba(0, 0, 0, 0.22);
  user-select: none;
  
  &:hover {
    transform: scale(1.05);
  }
`;
const RecipeHeader = styled.p`
  color: ${({ theme }) => theme.colors.neutral[400]};
  font-size: 22px;
  font-weight: bold;
  text-transform: capitalize;
  text-align: center;
  margin-bottom: 15px;
`;
const DetailWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 8px 0;
`;
const Line = styled.hr`
  border: 1px solid ${({ theme }) => theme.colors.neutral[200]};
  border-radius: 20px;
`;

const Recipes = () => {
  const [coffee, setCoffee] = useState([]);
  const [loading, setLoading] = useState(true);

  console.log(coffee);

  const fetchCoffee = async () => {
    const { data } = await axios.get('coffee');
    if (data) {
      setCoffee(data);
    }
    setLoading(false);
  };

  useEffect(() => {
    fetchCoffee();
  }, []);
  return (
    <Workspace>
      <section>
        <Header huge>Przepisy</Header>
        <Flex>
          {loading ? <Spinner/> : (
            coffee.map(coffeeItem => (
              <RecipeWrapper key={`coffee-${coffeeItem.id}`}>
                <RecipeHeader>{coffeeItem.name}</RecipeHeader>
                <DetailWrapper>
                  <Label>Status</Label>
                  <Status status={coffeeItem.status}/>
                </DetailWrapper>
                <DetailWrapper>
                  <Label>Koszt przygotowania</Label>
                  <div>{`${coffeeItem.productionCost} zł`}</div>
                </DetailWrapper>
                <DetailWrapper>
                  <Label>Marża</Label>
                  <div>{`${coffeeItem.priceMargin.toFixed(2)} zł`}</div>
                </DetailWrapper>
                {coffeeItem.recipe.length !== 0 && (
                  <>
                    <Line/>
                    <Label>Przepis</Label>
                    {coffeeItem.recipe.map(recipeItem => {
                      const unit = recipeItem.unit === 'kilogram' ? 'kg' : recipeItem.unit;
                      return (
                        <RecipeItem
                          key={recipeItem.name}
                          name={recipeItem.name}
                          amount={recipeItem.amount}
                          unit={unit}
                        />
                      )
                    })}
                  </>
                )}
              </RecipeWrapper>
            ))
          )}
        </Flex>
      </section>
      <section>.</section>
    </Workspace>
  )
};

export default Recipes;